@font-face {
  font-family: Quicksand;
  src: url('./assets/fonts/Quicksand-Regular.otf');
}
@import 'pdok-huisstijl/dist/pdok-huisstijl/css/pdok-huisstijl.css';


html, body {
  margin: 0px;
  background: #f5f5f5;
  font-family: Quicksand;
}

.navbar-brand img {
  height: 30px;
}

#tileSetError{
  display:inline-block;
}

#map {
    background-color: white;
  flex:1;
}

#sidebar {
  padding:0.5em;
  display: flex;
  flex:1;
  flex-flow: column;
  align-items: stretch;
  background-color: white;
  margin-left: 5px;
  overflow: hidden;
}

#sourceControl{
  position: absolute;
  top: .5em;
  left: .5em;
  width: 25em
}

select:disabled{
  color: gray;
}

#sourceInputButton{
  border: solid 1px #CFD6E6;
  border-radius: 0 3px 3px 0;
  border-left: 0px;
  width: 3em;
}

#sourceInput{
  border: solid 1px #CFD6E6;
  border-radius: 3px 0 0 3px;
  width: 21em;
  padding-left: .5em;
}


@media only screen and  (min-width: 0px) and (max-width: 640px)  {
  .select-wrapper, #styleSelect{
    width: 90vw;
  }
  #sourceControl{
    width: 80vw;
  }
  #sourceInput{
    width:calc(80vw - 3em) !important;
  }
}

@media only screen and (min-width: 640px) and (max-width: 1024px)  {
  #sidebar{
    max-width: 21em;
  }
  .select-wrapper, #styleSelect{
    width: 19em;
  }
  #sourceControl{
    width: 19em;
  }
  #sourceInput{
    width:16em;
  }
}


@media only screen and (min-width: 1024px)  {
  #sidebar{
    max-width: 35em;
  }
  .select-wrapper, #styleSelect{
    width: 33em;
  } 
}

#mapwrapper {
  flex:1;
  display: flex;
  flex-direction: row;
  padding: 5px;
}

html, body, #content {
  height: 100%;
}

.error {
  color: red;
}

#sidebarinfo {
  overflow: auto;
  flex-grow: 1;
  position: relative;
  background-color: white;
  padding: .5em;
}

#stylebox{
  padding: .5em;  
}


.container {
  margin-right: unset;
  margin-left: unset;
}

.gfitable tr:nth-child(even) {
  background: #F4F4F4
}

.gfitable tr:nth-child(odd) {
  background: white
}

.gfitable {
  width: 100%;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 2s linear infinite;
  display: inline-block;
}


@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.leaflet-container {
  background-color: rgb(247, 247, 247);
}

.arrow_box {
  position: relative;
  background: #000;
  border: 1px solid #003c88;
}

.arrow_box:after, .arrow_box:before {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.arrow_box:after {
  border-color: rgba(0, 0, 0, 0);
  border-top-color: #000;
  border-width: 10px;
  margin-left: -10px;
}

.arrow_box:before {
  border-color: rgba(0, 60, 136, 0);
  border-top-color: #003c88;
  border-width: 11px;
  margin-left: -11px;
}

.arrow_box {
  border-radius: 5px;
  padding: 10px;
  opacity: 0.8;
  background-color: black;
  color: white;
}

#popup-content {
  max-height: 200px;
  overflow: auto;
}

#popup-content th {
  text-align: left;
  width: 125px;
}

#info ul {
  list-style-type: none;
  padding: unset;
}

#info{
  margin-top: auto;
  text-align: center;
  position: relative;
  bottom: 0px;
}

#sidebartop {
  display: flex;
  flex-flow: column nowrap;  
}


.form-label{
  font-weight: unset;
  margin-left:.4em;
  margin-right:.4em;
}

label:hover{
  text-decoration:unset;
}

.form-control::placeholder{
  color: #a7a5a5;
}

#sourceInputButton, #sourceInput, #styleSelect{
  height: 36px; 
}


#btnAppOpen{
  display:none;
  right: .5em;
  float:right;
  top: .5em;
  display: block;
  border: none;
  color: white;
  font-weight: bold;
  border-radius: 2px;
}

.ol-rotate{
  right: .5em !important;
  top: 42px !important;
}

#btnAppCloseWrapper{
  position: absolute;
  right: .5em;
  display: none;
}

.navbar-header{
  padding: 2px 2px 2px 10px;
}

#info hr{
  width: 90%;
  margin-top: 5px;
  margin-bottom: 5px;
}

* { box-sizing: border-box; }

.autocomplete {
  /*the container must be positioned relative:*/
  position: absolute;
  /* display: inline-block; */
}

.autocomplete-items {
  position: absolute;
  border: 1px solid #d4d4d4;
  border-bottom: none;
  border-top: none;
  z-index: 1000;
  /*position the autocomplete items to be the same width as the container:*/
  left: 0;
  right: 0;
  /* overflow: scroll; */
}
.autocomplete-items div {
  padding: 10px;
  cursor: pointer;
  background-color: #fff;
  border-bottom: 1px solid #d4d4d4;
  overflow: hidden;
}

.autocomplete-items div:first-child {
  border-top: 1px solid #d4d4d4;
}

.autocomplete-items div:hover {
  /*when hovering an item:*/
  background-color: #e9e9e9;
}
.autocomplete-active {
  /*when navigating through the items using the arrow keys:*/
  background-color: DodgerBlue !important;
  color: #ffffff;
}



.ol-zoom{
  top: 3em !important;
}



#autocompleteWrapper {
	position: absolute;
	margin-top: 3em;
	z-index: 1000;
  width: 100%;
}

button{
  background-color: #F5F6F7;
}

select{
  background-color: #F5F6F7;
  border: solid 1px #CFD6E6;
  border-radius: 3px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 5px 35px 5px 5px;
  /* background: url(http://www.stackoverflow.com/favicon.ico) 96% / 15% no-repeat #eee; */
}

#selectStyle{
  padding: 10px 20px;
}

.select-wrapper {
  position: relative;
}

.select-wrapper:after {
  content: '\f0d7';
  font-family: FontAwesome;
  color: #1A1E4F;
  display: inline-block;
  position: absolute;
  top: 0.5em;
  right: 1em;
  pointer-events: none;
  size: 1em;
}